body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navigation-bar {
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    background-color: #f3f3f3;
    color: #333;
    padding: 20px 0px;
  }
  .navigation-bar ul{
    display: -webkit-inline-flex;
    display: inline-flex;
    list-style: none;
    margin: 0;
  }
  .navigation-bar li{
    margin-right: 20px;
    cursor: pointer;
  }
  .navigation-bar li.active-route{
    color: rgb(3, 3, 128);
    font-weight: 900;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    -webkit-text-decoration-style: double;
            text-decoration-style: double;
  }
.footer {
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    background-color: #333;
    color: white;
    padding: 10px 20px;
  }

.personal-profile {
    height: 100vh;
    width: 100vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #fff;
}
.profile-content {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  overflow-y: auto;
  padding: 20px;
}



h1, h2{
    text-align: center;
    padding: 0;
    margin: 0;
}
h1{
    font-size: 2em;
}
h2{
    font-size: 1.5em;
    background-color: #030380;
    color: white;
    border-radius: 4px;
}
.contacts-wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    height: 100%;
    border: 1px solid;
}
@media only screen and (max-width: 375px) {
    .contacts-wrapper{
        -webkit-flex-direction: column;
                flex-direction: column;
    }
}

.personal-contacts{
    -webkit-flex-grow: 1;
            flex-grow: 1;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;   
    margin: 15px;
}

.contact-details{
    padding-left: 20px;
}

.phone-wrapper, 
.email-wrapper{
    -webkit-flex:1 1;
            flex:1 1;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.actions-icon-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-flex-direction: row;
            flex-direction: row;
    font-size: 6em;
    width: 100%;
}
.actions-icon {
    -webkit-flex: 1 1;
            flex: 1 1;
    display:inline;
    display:initial;
    text-align: center;
}
.flip-card-wrapper{
    -webkit-perspective: 1000px;
            perspective: 1000px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    z-index: auto;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.flip-card{
    position: relative;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    height: 100%;
    border-radius: 5px;
    -webkit-flex: 1 1;
            flex: 1 1;
    display: -webkit-flex;
    display: flex;
    color: #fff;
    text-align: center;
}

.flip-card-front, .flip-card-back{
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    left: 0px;
    top: 0px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    width: 100%;
    background: rgb(3, 3, 128);
}

.flip-card-content{
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 1 1;
            flex: 1 1;
    overflow: hidden;
}

.social-contacts{
    margin: 15px;
}
html {
  height: 100%;
  font-family: sans-serif;
}
body {
  height: 100%;
  overflow: hidden;
  margin: 0px;
  display: -webkit-flex;
  display: flex;
}

